import React, { Suspense } from "react";
import { Canvas } from "@react-three/fiber";
import { OrbitControls, Preload, useGLTF } from "@react-three/drei";
import CanvasLoader from "../Loader";
const Earth = () => {
  const earth = useGLTF("./planet/scene.gltf");
  return <primitive object={earth.scene} scale={2} position-y={0} rotation-y={0} data-sentry-element="primitive" data-sentry-component="Earth" data-sentry-source-file="Earth.jsx" />;
};
const EarthCanvas = () => {
  return <Canvas shadows frameloop="demand" gl={{
    preserveDrawingBuffer: true
  }} camera={{
    fov: 45,
    near: 0.1,
    far: 200,
    position: [-4, 3, 6]
  }} data-sentry-element="Canvas" data-sentry-component="EarthCanvas" data-sentry-source-file="Earth.jsx">
      <Suspense fallback={<CanvasLoader />} data-sentry-element="Suspense" data-sentry-source-file="Earth.jsx">
        <OrbitControls autoRotate={true} autoRotateSpeed={4} enableZoom={false} maxPolarAngle={Math.PI / 2} minPolarAngle={Math.PI / 2} data-sentry-element="OrbitControls" data-sentry-source-file="Earth.jsx" />
      </Suspense>
      <Earth data-sentry-element="Earth" data-sentry-source-file="Earth.jsx" />
      <Preload all data-sentry-element="Preload" data-sentry-source-file="Earth.jsx" />
    </Canvas>;
};
export default EarthCanvas;