import { useState, useRef, Suspense } from "react";
import { Canvas, useFrame } from "@react-three/fiber";
import { Points, PointMaterial, Preload } from "@react-three/drei";
import * as random from "maath/random/dist/maath-random.esm";
const Stars = props => {
  const ref = useRef();
  const [sphere] = useState(() => random.inSphere(new Float32Array(5000), {
    radius: 1.2
  }));
  useFrame((state, delta) => {
    ref.current.rotation.x -= delta / 10;
    ref.current.rotation.y -= delta / 15;
  });
  return <group rotation={[0, 0, Math.PI / 4]} data-sentry-element="group" data-sentry-component="Stars" data-sentry-source-file="Stars.jsx">
      <Points ref={ref} positions={sphere} stride={3} frustumCulled {...props} data-sentry-element="Points" data-sentry-source-file="Stars.jsx">
        <PointMaterial transparent color='#f272c8' size={0.0028} sizeAttenuation={true} depthWrite={false} data-sentry-element="PointMaterial" data-sentry-source-file="Stars.jsx" />
      </Points>
    </group>;
};
const StarsCanvas = () => {
  return <div className='w-full h-auto absolute inset-0 z-[-1]' data-sentry-component="StarsCanvas" data-sentry-source-file="Stars.jsx">
      <Canvas camera={{
      position: [0, 0, 1]
    }} data-sentry-element="Canvas" data-sentry-source-file="Stars.jsx">
        <Suspense fallback={null} data-sentry-element="Suspense" data-sentry-source-file="Stars.jsx">
          <Stars data-sentry-element="Stars" data-sentry-source-file="Stars.jsx" />
        </Suspense>

        <Preload all data-sentry-element="Preload" data-sentry-source-file="Stars.jsx" />
      </Canvas>
    </div>;
};
export default StarsCanvas;