"use client";

import React, { useState, useRef, useEffect } from "react";
import { motion, useAnimationFrame, useMotionTemplate, useMotionValue, useTransform } from "framer-motion";
import { cn } from "@/lib/utils";
export function Button({
  borderRadius = "1.75rem",
  children,
  as: Component = "button",
  containerClassName,
  borderClassName,
  duration,
  className,
  ...otherProps
}: {
  borderRadius?: string;
  children: React.ReactNode;
  as?: React.ElementType;
  containerClassName?: string;
  borderClassName?: string;
  duration?: number;
  className?: string;
  [key: string]: any;
}) {
  const [dark, setdark] = useState(false);
  useEffect(() => {
    let mode = localStorage.getItem("theme") || "system";
    if (mode === "dark") {
      setdark(true);
    } else {
      setdark(false);
    }
  }, []);
  return <Component className={cn("bg-transparent relative text-xl md:col-span-2 p-[1px] overflow-hidden", containerClassName)} style={{
    borderRadius: borderRadius
  }} {...otherProps} data-sentry-element="Component" data-sentry-component="Button" data-sentry-source-file="MovingBorder.tsx">
      <div className="absolute inset-0" style={{
      borderRadius: `calc(${borderRadius} * 0.96)`
    }}>
        <MovingBorder duration={duration} rx="30%" ry="30%" data-sentry-element="MovingBorder" data-sentry-source-file="MovingBorder.tsx">
          <div className={cn("h-20 w-20 dark:opacity-[0.8] bg-[radial-gradient(var(--violet-500)_40%,transparent_60%)]", borderClassName)} />
        </MovingBorder>
      </div>

      <div className={cn("relative dark:bg-slate-900/[0.8]", "text-black-100 dark:text-white", "border border-slate-900 backdrop-blur-xl", "flex items-center justify-center w-full h-full text-sm antialiased", dark ? "" : "bg-dot-white-100", className)} style={{
      borderRadius: `calc(${borderRadius} * 0.96)`
    }}>
        {children}
      </div>
    </Component>;
}
export const MovingBorder = ({
  children,
  duration = 2000,
  rx,
  ry,
  ...otherProps
}: {
  children: React.ReactNode;
  duration?: number;
  rx?: string;
  ry?: string;
  [key: string]: any;
}) => {
  const pathRef = useRef<any>();
  const progress = useMotionValue<number>(0);
  useAnimationFrame(time => {
    const length = pathRef.current?.getTotalLength();
    if (length) {
      const pxPerMillisecond = length / duration;
      progress.set(time * pxPerMillisecond % length);
    }
  });
  const x = useTransform(progress, val => pathRef.current?.getPointAtLength(val).x);
  const y = useTransform(progress, val => pathRef.current?.getPointAtLength(val).y);
  const transform = useMotionTemplate`translateX(${x}px) translateY(${y}px) translateX(-50%) translateY(-50%)`;
  return <>
      <svg xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="none" className="absolute h-full w-full" width="100%" height="100%" {...otherProps} data-sentry-element="svg" data-sentry-source-file="MovingBorder.tsx">
        <rect fill="none" width="100%" height="100%" rx={rx} ry={ry} ref={pathRef} data-sentry-element="rect" data-sentry-source-file="MovingBorder.tsx" />
      </svg>
      <motion.div style={{
      position: "absolute",
      top: 0,
      left: 0,
      display: "inline-block",
      transform
    }} data-sentry-element="unknown" data-sentry-source-file="MovingBorder.tsx">
        {children}
      </motion.div>
    </>;
};